<template>
  <div class="app-container">
    <!--  -->
    <div class="filter-container">
      <el-input style="width: 200px;" class="filter-item" placeholder="" v-model="listQuery.orderId">
      </el-input>

      <el-select clearable class="filter-item" style="width: 130px" v-model="listQuery.type" placeholder="">
        <el-option v-for="item in  orderStatus" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>

      <!--popover-->
      <el-popover
        ref="advancedSearchPopover"
        placement="top-start"
        width="1000"
        trigger="click"
        v-model="showAdvancedSearchPopover">
        <el-form label-width="80px" label-position="right">
          <el-row>
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="" v-model="listQuery.orderId" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="">
                <el-date-picker
                  type="datetimerange"
                  placeholder=""
                  clearable
                  align="right" v-model="listQuery.orderTime">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
      
          <el-row>
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="/" v-model="listQuery.customerInfo" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
        
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="/" v-model="listQuery.goodsInfo" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
 
          <el-select clearable class="filter-item" style="width: 150px; margin-left:80px; margin-bottom: 20px;" v-model="listQuery.type" placeholder="">
            <el-option v-for="item in  orderStatus" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
      
          <el-form-item>
            <el-button type="primary" size="small" @click="handleFilter"></el-button>
            <el-button size="small" @click="showAdvancedSearchPopover = false"></el-button>
          </el-form-item>
        </el-form>
      </el-popover>

      <el-button class="filter-item" type="primary" v-waves icon="search" @click="handleFilter"></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="search" v-popover:advancedSearchPopover size="small"></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="document"></el-button>    
    </div>  
    <div class="top-distance">
      <el-row>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves></el-button>
        <el-button class="filter-item" type="primary" v-waves icon="edit" v-popover:sendGoodsPopover @click="handleSendAll"></el-button>
        <el-button class="filter-item" type="primary" v-waves icon="edit" @click="handleDelAll"></el-button>
      </el-row>
    </div>  
    <!--  -->
    <el-table ref="multipleTable" @selection-change="handleSelectionChange" :data="list" v-loading.body="listLoading" element-loading-text="" border fit highlight-current-row  >
         <el-table-column type="selection" width="55">
         </el-table-column>

          <!-- <el-table-column align="center" label='' width="90">
            <template scope="scope">
              {{scope.$index}}
            </template>
          </el-table-column> -->

          <el-table-column label="">
            <template scope="scope">
              {{scope.row.orderId}}
            </template>
          </el-table-column>

          <el-table-column align="center"  label="">
              <template scope="scope">
                <span>{{scope.row.goods}}</span>
              </template>
          </el-table-column>

          <el-table-column align="center"  label="">
              <template scope="scope">
                <span>{{scope.row.money}}</span>
              </template>
          </el-table-column>

          <el-table-column label="">
            <template scope="scope">
              <span>{{scope.row.realName}}</span>
            </template>
          </el-table-column>

          <el-table-column align="center" prop="created_at" label="" width="170">
            <template scope="scope">
              <span>{{scope.row.orderDay}}</span>
            </template>
          </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <span>{{scope.row.orderNum}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <span>{{scope.row.collectionMoney}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <el-tag :type="scope.row.orderStatus | statusFilter">{{scope.row.orderStatus | statusFilterTip}}</el-tag>
                </template>
            </el-table-column>


            <el-table-column align="center"  label="" width="150">
                <template scope="scope">
                   <el-button class="edit" size="small" v-waves @click="handleEdit(scope.$index, scope.row)"></el-button>
                   <el-button class="delete" size="small" v-waves type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
    </el-table>
    <!--  -->
    <div v-show="!listLoading" class="pagination-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.currPage"
        :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {api} from '@/global/api'
  export default {
    data() {
      return {
        list: null,//list
        total: null,
        listLoading: false,
        showAdvancedSearchPopover: false,
        isIndeterminateStatus: false,
        checkAllStatus: true,
        checkedStatuss: [0, 1],
        listQuery: { //
          currPage: 1,
          pageSize: 10,
          orderId: '',
          orderTime: null,
          customerInfo: null,
          goodsInfo: null,
          type: null,     
        },
        orderStatus: [{label: '', value: 1}, {label: '', value: 2}, {label: '', value: 3}, {label: '', value: 4}, {label: '', value: 5}],
        dialogFormVisible: false,
        multipleSelection: []  //
      }
    },
    filters: {
        parseTime(time) {
          return parseTime(time);
        },
        statusFilter(status) {
            const statusMap = ['primary', 'success', 'warning', 'danger', 'gray'];
            return statusMap[status - 1];
        },
        statusFilterTip(status) {
            const statusMap = ['', '', '', '', ''];
            return statusMap[status - 1];
        }
    },
    mounted() {
      var vm = this;
      vm.getList();
    },
    methods: {
      //
      getList: function() {
        var vm = this;
        //
        console.log(JSON.stringify(vm.listQuery));
        this.$http.get(api.orderInfo,{params: vm.listQuery}).then(function(response) {
            var data = response.body;
            vm.list = data.data.data;
            vm.listQuery.currPage = data.data.currPage;
            vm.listQuery.pageSize = data.data.pageSize;
            vm.total = data.data.total;
            vm.listLoading = false;   
          }, function(response) {
            alert("");
          })
        },
      //
      handleEdit(index,row){
        var vm = this;
        console.log('row',index,'-----',row);
        //
        //this.$router.push('/example/form'); 
        this.$router.push( { path: '/index/order/editOrder', query: { id: row.orderId } } ); //
      },
      //
      handleDelete(index,row){
          var vm = this;
          console.log('row',index,'-----',row);
          //
          vm.list.splice(index,1)
      },
      //
      handleDelAll(){
          var vm = this;
          console.log('row',vm.multipleSelection);
      },
      //
      handleSendAll() {
        var vm = this;
        console.log('row',vm.multipleSelection);
        this.$router.push('/index/order/editOrder' ); //
      },
      //
      handleFilter() {
        this.showAdvancedSearchPopover = false;
        this.getList();
      },
      //
      handleCheckAllStatusChange(event) {
        let statuss = [];
        for (let tag of this.orderStatus) {
          statuss.push(tag.value);
        }
        this.checkedStatuss = event.target.checked ? statuss : [];
        this.isIndeterminateStatus = false;
      },
      handleCheckedStatusChange(value) {
        let checkedCount = value.length;
        this.checkAllStatus = checkedCount === this.orderStatus.length;
        this.isIndeterminateStatus = checkedCount > 0 && checkedCount < this.orderStatus.length;
      },
      //
      handleSizeChange(val) {
        this.listQuery.pageSize = val;
        this.getList();
      },
      //
      handleCurrentChange(val) {
        //console.log('--------',val)
        this.listQuery.currPage = val;
        this.getList();
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
      },
      // handleDownload() {
      //   var vm = this;
      //   require.ensure([], () => {
      //     const { export_json_to_excel } = require('vendor/Export2Excel');
      //     const tHeader = ['', '', '', '', '', '', '', ''];
      //     const filterVal = ['orderId', 'goods', 'money', 'realName', 'orderDay', 'orderNum', 'collectionMoney', 'orderStatus'];
      //     const list = vm.list;
      //     const data = vm.formatJson(filterVal, list);
      //     export_json_to_excel(tHeader, data, 'excel');
      //   })
      // },
      // formatJson(filterVal, jsonData) {
      //   return jsonData.map(v => filterVal.map(j => v[j]))
      // }
    }
  };
</script>
<style>
  .app-container {
    width: 1210px;
    padding-left: 20px;
    padding-top: 25px;
  } 
  @media only screen and (max-width: 1400px) {
    .app-container {
      width: 1120px;
      padding-top: 20px;
    } 
  }
  .el-table {
    margin-top: 20px;
  } 
  .edit {
    float: left;
  }
  .delete {
    float: right;
  }
  .pagination-container {
    margin-top: 15px;
  }
  .top-distance {
    margin-top: 15px;
  }
</style>


